import React, { useRef, useState } from 'react';
import './contact.css';
import emailjs from '@emailjs/browser';
import { motion } from 'framer-motion';

const Contact = () => {
  const form = useRef();
  const [isSending, setIsSending] = useState(false);
  const [isSent, setIsSent] = useState(false);

  const sendEmail = async (e) => {
    e.preventDefault();
    setIsSending(true);

    try {
      await emailjs.sendForm(
        'service_3dmhj8i',
        'template_dyieh57',
        form.current,
        'rPgodyhZzFg51oX2e'
      );
      setIsSent(true);
      e.target.reset();
    } catch (error) {
      console.error('Error:', error); // Log the error object
      alert('Failed to send the message. Please try again later.');
    } finally {
      setIsSending(false);
      setTimeout(() => setIsSent(false), 3000);
    }
  };

  return (
    <section className="contact section" id="contact">
      <h2 className="section__title">Get in touch</h2>
      <span className="section__subtitle">Contact Me</span>

      <div className="contact__container container grid">
        {/* Contact Info Section */}
        <div className="contact__content">
          <h3 className="contact__title">Talk to me</h3>
          <div className="contact__info">
            <div className="contact__card">
              <i className="bx bx-mail-send contact__card-icon"></i>
              <h3 className="contact__card-title">Email</h3>
              <span className="contact__card-data">upadhyayarsh23@gmail.com</span>
              <a href="mailto:upadhyayarsh23@gmail.com" className="contact__button">
                Write me <i className="bx bx-right-arrow-alt contact__button-icon"></i>
              </a>
            </div>
          </div>
        </div>

        {/* Contact Form Section */}
        <div className="contact__content">
          <h3 className="contact__title">Write me your project</h3>
          <form ref={form} onSubmit={sendEmail} className="contact__form">
            <div className="contact__form-div">
              <label className="contact__form-tag">Name</label>
              <input
                type="text"
                name="name"
                className="contact__form-input"
                placeholder="Your name"
                required
              />
            </div>
            <div className="contact__form-div">
              <label className="contact__form-tag">Mail</label>
              <input
                type="email"
                name="email"
                className="contact__form-input"
                placeholder="Your email"
                required
              />
            </div>
            <div className="contact__form-div contact__form-area">
              <label className="contact__form-tag">Project</label>
              <textarea
                className="contact__form-input"
                name="message"
                cols="30"
                rows="10"
                placeholder="Tell me about your project"
                required
              ></textarea>
            </div>
            <button type="submit" className="button button--flex" disabled={isSending}>
              {isSent ? (
                <>
                  Message Sent
                  <motion.svg
                    className="button__icon"
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                    initial={{ opacity: 0, scale: 0.8 }}
                    animate={{ opacity: 1, scale: 1 }}
                    transition={{ duration: 0.5 }}
                  >
                    <path
                      d="M20.285 6.075l-1.415-1.414L9 14.53 5.13 10.66l-1.415 1.414L9 17.36z"
                      fill="var(--container-color)"
                    ></path>
                  </motion.svg>
                </>
              ) : isSending ? (
                <>
                  Sending...
                  <motion.div
                    className="spinner-container"
                    initial={{ rotate: 0 }}
                    animate={{ rotate: 360 }}
                    transition={{ repeat: Infinity, duration: 1 }}
                    style={{
                      width: '24px',
                      height: '24px',
                      border: '4px solid transparent',
                      borderTopColor: '#FF5349',
                      borderRadius: '50%',
                      marginLeft: '10px',
                    }}
                  ></motion.div>
                </>
              ) : (
                <>
                  Send Message
                  <svg
                    className="button__icon"
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                  >
                    <path
                      d="M14.2199 21.9352C13.0399 21.9352 11.3699 21.1052 10.0499 17.1352L9.32988 14.9752L7.16988 14.2552C3.20988 12.9352 2.37988 11.2652 2.37988 10.0852C2.37988 8.91525 3.20988 7.23525 7.16988 5.90525L15.6599 3.07525C17.7799 2.36525 19.5499 2.57525 20.6399 3.65525C21.7299 4.73525 21.9399 6.51525 21.2299 8.63525L18.3999 17.1252C17.0699 21.1052 15.3999 21.9352 14.2199 21.9352Z"
                      fill="var(--container-color)"
                    ></path>
                  </svg>
                </>
              )}
            </button>
          </form>
        </div>
      </div>
    </section>
  );
};

export default Contact;
